import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Collapse, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, ButtonDropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'reactstrap'
import { getTransactionName ,getShareHost} from '../js/vgoUtil.js'
import AccountDialog from '../../conponents/AccountDialog'
const get = require('simple-get')
class UnconfirmedList extends Component {

  constructor(props) {
    super(props);
    this.page = 1;
    this.state = { list: [], page: 1, count: 0 }
  }

  componentDidMount() {
    this.loadUnconfirmedList(1)
  }

  loadUnconfirmedList(page) {
    let url = getShareHost() + '/vgo?action=unconfirmedList&page=' + page;

    get.concat(url, (function (err, res, data) {
      if (err) {
        // console.log(err);
        return;
      }
      try {
        let pdata = JSON.parse(data);
        let list = pdata.transactions;
        let count = pdata.count;
        this.setState({ list, count })
      } catch (e) { }
    }).bind(this))
  }

  prePage() {
    if (this.state.page > 1) {
      this.state.page--
      this.loadUnconfirmedList(this.state.page)
    }
  }

  nextPage() {
    this.state.page++
    this.loadUnconfirmedList(this.state.page)
  }

  renderBlockList() {
    let list = this.state.list;
    if (!list)
      return null;
    let content = list.map((res) => {
      return (<tr>
        <td>{getTransactionName(res.type)}</td>
        <td><AccountDialog rsId={res.from} /></td>
        <td><AccountDialog rsId={res.to} /></td>
        <td>{res.amount} VGo</td>
        <td>{res.fee} VGo</td>
        <td>{res.height}</td>
      </tr>)
    });

    return content;
  }

  renderList() {
    let list = this.state.list;
    if (!list)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <Table hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>Type</th>
                  <th>From</th>
                  <th>To</th>
                  <th>Amount</th>
                  <th>Fee</th>
                  <th>Height</th>
                </tr>
              </thead>
              <tbody>
                {this.renderBlockList()}
              </tbody>
            </Table>
            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>Prev</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page} - Total({this.state.count})</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>Next</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  render() {
    return (
      <div className="animated fadeIn">
        {this.renderList()}
      </div>

    );
  }
}

export default UnconfirmedList;



